import * as React from "react";
import { getCardList } from '../../api/card'
import FootAdvertising from '../../components/advertising/footAdvertising'
import CardTime from '../../components/card/cardTime'
import NoCard from '../../components/card/noCard'
import { getOneTimeChina } from '../../util'
import { getUserInfo } from '../../api/bind'

import mine6 from '../../assets/img/mine/mine6.png';
import './index.less'
import { Toast } from "antd-mobile";

interface ISprops {
}
interface IState {
    [statesName: string]: any
}

const CardMoney = (props: any) => {
    let data = props.data;
    let time = getOneTimeChina(data.end_time)
    return (
        <div className="card-item">
            <div className="card-money">
                <p className="tit">
                    {data.company_name}
                </p>
                <p className="money">
                    总金额 ￥<span>{data.total_amount}</span>元
                </p>
                <p className="time">
                    {data.card_no}
                </p>
                <p className="type">
                    储值卡
                </p>
            </div>
            <div className="detail">
                <div className="detail-i">
                    <p>{time}</p>
                    有效期
                </div>
                <div className="detail-i">
                    <p>￥{data.surplus_amount}</p>
                    剩余金额
                </div>
            </div>
        </div>

    )
}
const CheckCard = (props: any) => {
    let type = props.cardData.cost_type;
    let data = props.cardData;
    return (
        <React.Fragment>
            {
                type === 1 ?
                    <CardMoney data={data} />
                    :
                    <CardTime data={data} type={true} />
            }

        </React.Fragment>

    )
}

export default class CardIndex extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            cardData: [],
            type: true,
            customer: {},
            isLoading: true
        }
    }
    getCard () {
        Toast.loading('')
        getUserInfo({}).then( res => {
            if (!res.status) {
                this.setState({customer: res.data.customer || {}})
            } 
        })
        getCardList({}).then((data) => {
            Toast.hide()
            this.setState({isLoading: false})
            if (!data.status) {
                this.setState({
                    cardData: data.data || []
                })
            }
        })
    }
    componentDidMount() {
        document.title = '会员卡';
        this.getCard()
    }
    render() {
        const { cardData, type, customer, isLoading } = this.state;
        console.log(customer)
        return (
            <div className="card-index">
                {
                    cardData.length === 0 && !isLoading?
                        <NoCard />
                        :
                        <div className="card-detail">
                            {
                                cardData.map((el, index) => {
                                    return (
                                        <CheckCard key={index} cardData={el} />
                                    )

                                })
                            }

                            {
                                Object.keys(customer).length === 0 ? 
                                    <div></div>
                                    :
                                    <div className="card-control clearfix">
                                        <img src={mine6} alt="" className="left" />
                                        <div className="card-control-r">
                                            <a href={`tel:${customer.phone}`}>
                                                <div>专属会员管家  <span>{customer.name}</span></div>
                                                <p>{customer.phone}</p>
                                                <p>{customer.email} </p>
                                            </a>
                                        </div>
                                    </div>
                            }
                            

                        </div>
                }
                <FootAdvertising  title="vip-card"/>

            </div>
        )
    }

}
